// Пути

$fonts: "../fonts";
$img: "../img";

// Цвета

$color-black: #000000;
$color-white: #ffffff;
$red: #FF6663;
$blue: #0E4E96;
$light-blue: #17A6E8;
$gray-five: #555555;

$g-orange: linear-gradient(to right, #E88D17, #FF6663);
$g-blue: linear-gradient(to right, #17A6E8, #17A6E8);


$tablet: max-width 768px;
$phone: max-width 624px;
$big: 3840px;

.sec-h3 {
  color: $blue;
  font-size: 32px;
  letter-spacing: 4px;
  margin: 0;
  margin-bottom: 30px;
  font-weight: 600;
}

.sec-ul {
  color: $gray-five;
  padding-left: 0px;
  margin: 0;
  font-size: 17px;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  li {
    margin-bottom: 20px;
  }
}

.btn-red {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: white;
  text-decoration: none;
  width: 40%;
  min-width: 300px;
  height: 40px;
  border: 1px solid $red;
  border-radius: 20px;
  color: $red;
  transition: 300ms ease;
  &:hover {
    color: $light-blue;
    border-color: $light-blue;
  }
}
