@import "global/variables.scss";
@import "global/fonts.scss";
@import "global/mixins.scss";
@import "global/patterns/**/*.scss";
@import "global/defaults.scss";
@import "global/breakpoint.scss";

@import "components/**/*.scss";
@import "level-1/**/*.scss";

body,html {
  width: 100vw;
  margin: auto;
  overflow: hidden;
  overflow-y: scroll;
  font-family: 'Noto Sans TC', Arial, Helvetica, sans-serif;
}

nav {
  width: 100vw;
  height: 100px;
  position: fixed;
  top: 0;
  z-index: 99;
  background: white;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  box-shadow: 0px 3px 4px 0px rgba(0,0,0,0.1);
  @include breakpoint($phone) {
    height: 50px;
    .header-logo {
      width: 55%;
    }
  }
  .wrap {
    width: 100%;
    max-width: 1024px; 
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    .btn-1 {
      text-decoration: none;
      position: relative;
      display: block;
      width: 210px;
      margin-top: -35px;
      transition: 400ms ease;
      @include breakpoint($tablet) {
        width: 140px;
      }
      .static {
        z-index: 99;
        transition: 300ms ease;
      }      
      img{
        position: absolute;
      }
      &:hover {
        .static {
          opacity: 0;
          transition: 300ms ease;
        }
      }
      @include breakpoint($phone) {
        display: none;
      }
    }
  }
  .btn-2 {
    display: none;
    transition: 300ms ease;
  @include breakpoint($phone) {
      display: block;
    }
  }

}

.banner {
  width: 100%;
  height: 605px;
  z-index: -1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
  background:
    url(../img/banner.png);
    background-color: $blue;
    background-repeat: no-repeat;
    background-position: center center;
    background-origin: content-box;
  @include breakpoint($big) {
    background-position: bottom center;
    background-size: cover;
  }
  img {
    object-fit: cover;
    object-position: center center;
  }
  @include breakpoint($phone) {
    height: 195px;
    background:
      url();
      background-color: #0E4E96;
  }
  .title {
    text-align: center;
    color: white;
    position: absolute;
    top: 50%; right: 50%;
    transform: translate(50%,-50%);
    h1 {
      font-size: 48px;
      white-space: nowrap;
      position: absolute;
      top:50%;
      right: 120%;
      transform: translate(50%,-180%);
    }
    h2 {
      font-weight: 400;
      text-align: center;
      letter-spacing: 3px;
      font-weight: 30px;
      white-space: nowrap;
      object-fit: contain;
      position: absolute;
      top: 200%;
      right: 10%;
      transform: translate(18%,60%);
      img {
        margin: 0 .5rem;
      }
    }
    @include breakpoint($tablet) {
      h1{font-size: 36px;}
      h2{font-size: 24px;transform: translate(50%,60%);}
      img {
        display: none;
      }
    }
    @include breakpoint($phone) {
      h1 {
        font-size: 24px;
        transform: translate(50%,-100%);
      }
      h2 {
        font-size: 18px;
        transform: translate(50%,100%);
      }
    }
  }
}

.intro {
  display: flex;
  justify-content: space-between;
  max-width: 1024px;
  margin: auto;
  margin-top: -50px;
  z-index: 9;
  padding: 0 24px;
  li {
    list-style-type: none;
    width: 22%;
    @include breakpoint($phone) {
      width: 48%;
      padding: 12px;
    }
  }
  @include breakpoint($phone) {
    margin-top: 50px;
    flex-wrap: wrap;
  }
  .icon {
    width: 100px;
    height: 100px;
    margin: auto;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: white;
    box-shadow: 0px 3px 6px 0px rgba(0,0,0,0.3);
  }
  h3,p {
    text-align: center;
  }
  h3 {
    color: $red;
    font-weight: 500;
    font-size: 27px;
    margin: 35px 0 27px 0;
  }
  p {
    color: $gray-five;
    font-weight: 300;
    font-size: 17px;
    line-height: 28px;
  }
}

.scroll {
  color: $red;
  font-weight: 300;
  margin: auto;
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  letter-spacing: 3px;
  @include breakpoint($phone) {
    margin-top: 10px;
    span {
      display: none;
    }
  }
  img {
    margin-top: 20px;
  }
}
.s {
  max-width: 1024px;
  margin: auto;
  display: flex;
  justify-content: space-between;
  overflow: visible;
  ul {
    list-style-type: none;
    li {
      &::before {
        content: "•";
        transform: scale(2);
        margin-right: 30px 0;
      }
    }
  }
  .right, .left {
    width: 48%;
    overflow: visible;
    @include breakpoint($tablet) {
      width: 80%;
      margin: auto;
      align-items: center;
      h3 {
        text-align: center;
      }
    }
  }
}
.s-l {
  @include breakpoint($tablet) {
    flex-direction: column-reverse;
  }
  .right {
   @include breakpoint($tablet) {
      display: flex;
      justify-content: center;
    }
  }
  .left {
    @include breakpoint($tablet) {
      text-align: center;
      justify-content: center;
      margin-top: 48px;
    }
    
  }
}
.s-r {
  @include breakpoint($tablet) {
    flex-direction: column;
  }
  @include breakpoint($tablet) {
    .right {
      margin-top: 48px;
      text-align: center;
      justify-content: center;
    }  
  }
  .left {
    @include breakpoint($tablet) {
      display: flex;
      justify-content: center;
    }
  }
}
.s-1 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 250px;
  @include breakpoint($tablet) {
    flex-direction: column-reverse;
    .left {
      margin-top: 200px;
      h3 {
        text-align: center;
      }
    }
  }
  @include breakpoint($phone) {
    margin-top: 180px;
    .left {
      margin-top: 120px;
    }
  }
  .right {
    position: relative;
    &::after {
      content: url(../img/bg-1.svg);
      position: absolute;
      top:50%;
      right: 50%;
      transform: translate(50%,-50%);
      z-index: -3;
      @include breakpoint($tablet) {
          width: unset;
          transform: translate(50%, -50%) scale(.8);
        }
    }
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      max-width: fit-content;
      margin: 3rem;
    }
    .slick {
      width: 144px;
      height: 261px;
      overflow: visible;
      position: relative;
      img {
        margin: 3rem 0 3rem 0;
      }
      .slick-arrow {
        position: absolute;
        top: 25%;
        cursor: pointer;
      }
      .slick-prev {
        left: -55%;
      }
      .slick-next {
        right: -55%;
      }
      &:after {
        content: url(../img/v2-ui.svg);
        position: absolute;
        top:50%;
        right: 50%;
        transform: translate(50%,-252px);
        z-index: -1;
      }
    }
  }
}

.s-2 {
  margin-top: 280px;
  align-items: center;
  @include breakpoint($tablet) {
    margin-top: 100px;
  }
  .left {
  position: relative;
    img {
      box-shadow: 3px 3px 6px 0px rgba(0,0,0,0.3);
      border-radius: 2rem;
    }
    &::after {
      content: url(../img/bg-2.svg);
      position: absolute;
      top:50%;
      right: 50%;
      transform: translate(50%, -50%) scale(1);
      transform-origin: center center;
      z-index: -3;
      @include breakpoint($tablet) {
        width: 100vw;
        transform: translate(28%, -50%) scale(.5);
      }
      @include breakpoint ($phone) {
        display: none;
      }
    }
  }
  .right {
    margin-left: 5rem;
    @include breakpoint($tablet) {
        margin-left: 0;
        display: flex;
        flex-direction: column;
        margin: auto;
        margin-top: 35px;
      }
    .img-wrap {
      display: flex;
      margin-top: 35px;
      @include breakpoint($tablet) {
        justify-content: center;
      }
      @include breakpoint($phone) {
        flex-direction: column;
      }
      img {
        
      }
      .wrap-1 {
        display: flex;
        flex-wrap: nowrap;
        @include breakpoint($phone) {
          transform: scale(1.2);
          transform-origin: center center;
          justify-content: center;
          img {
            margin: 0 1rem;
          }
        }        
      }
      .s-2-icon-3 {
        transform: scale(1.2) translateX(2rem);
        @include breakpoint($phone) {
          width: 100%;
          margin-top: 1rem;
          transform: scale(1) translateX(0rem);
        }
      }
    }
  }
}

.s-3 {
  margin-top: 280px;
  align-items: center;
  justify-content: space-between;
  @include breakpoint($phone) {
    margin-top: 120px;
  }
  .left {
    .img-wrap {
      margin-top: 35px;
      @include breakpoint($phone) {
        transform: scale(1.2);
        transform-origin: center;
      }
    }
  }
  .right {
    display: flex;
    img {
      margin-left: auto;
      margin-right: -1rem;
      @include breakpoint($tablet) {
      margin-right: 0;
      margin-left: -1.5rem;
      }
    }
    @include breakpoint($phone) {
      position: relative;
      &::after {
        content: url(../img/bg-2.svg);
        position: absolute;
        top:50%;
        right: 50%;
        transform: translate(50%, -50%) scale(.5);
        transform-origin: center center;
        z-index: -3;
      }
    }
  }
}

.s-4 {
  margin-top: 300px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: $gray-five;
  font-size: 17px;
  font-weight: 300;
  position: relative;
  @include breakpoint($tablet) {
    width: 80%;
    margin: auto;
  }
  @include breakpoint($phone) {
    margin-top: 100px;
  }
  &::after {
    content: url(../img/bg-4.svg);
    position: absolute;
    top:-33%;
    right: 50%;
    transform: translateX(50%);
    z-index: -3;
    @include breakpoint($phone) {
      width: 100vw;
      top: 20%;
      right: 80%;
      transform: translateX(50%) scale(.5);
    }
  }
  h3 {
    margin: 48px 0 10px 0;
    @include breakpoint($tablet) {
      text-align: center;
      margin: 24px 0 10px 0;
    }
  }
  p {
    max-width: 460px;
    text-align: center;
    line-height: 1.5rem;
  }
  .box-wrapper {
    display: flex;
    justify-content: space-between;
    max-width: 400px;
    width: 400px;
    align-items: center;
    .wrap {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      h4 {
        margin-bottom: 20px;
        align-items: center;
        color: $gray-five;
        font-size: 17px;
        font-weight: 300;
      }
      .box {
        color: $red;
        width: 180px;
        height: 60px;
        border: 1.5px solid $gray-five;
        border-radius: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        &.span {
          display: flex;
          flex-direction: column;
          &:before {
            content: attr(data-span);
            display: block;
            font-size: 10px;
          }
        }
      }
    }
    @include breakpoint($phone) {
      width: 100%;
      justify-content: space-between;
      .wrap {
        .box {
          width: 125px;
          padding: 0 .5rem;
        }
        img {
          @include breakpoint($tablet) {
            transform: scale(1.08);
            transform-origin: center center;
          }
        }
      }
    }
  }
  .btn-red {
    margin-top: 45px;
    position: relative;
    transition: 200ms ease;
    @include breakpoint($phone) {
      width: 80%;
      max-width: none;
    }
    &::before {
      content: "EMIS群豐電子發票";
      color: $light-blue;
      position: absolute;
      background: white;
      opacity: 0;
      transition: 200ms ease;
    }
    &:hover {
      &::before {
        opacity: 100;
      }
    }
  }
}

.s-5 {
  max-width: none;
  flex-direction: column;
  align-items: center;
  background: #EFF9FD;
  margin-top: 150px;
  padding: 72px;
  @include breakpoint($phone) {
    margin-top: 100px;
    text-align: center;
  }
  ul {
    margin: 30px 0;
  }
  .btn-red {
    width: 400px;
    @include breakpoint($phone) {
      width: 80%;
      max-width: none;
    }
  }
}

.s-6 {
  margin-top: 136px;
  .wrap {
    width: 30%;
    img {
      box-shadow: 3px 3px 6px 0px rgba(0,0,0,0.3);
      border-radius: 18px;
    }
    p {
      color: $blue;
      padding-left: 20px;
      margin: 0;
      font-size: 17px;
      font-weight: 300;
      -webkit-font-smoothing: antialiased;
      text-align: center;
      margin-top: 21px;
    }
  }
  @include breakpoint($tablet) {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .wrap {
      width: 80%;
      margin-bottom: 50px;
      img {
        width: 100%;
        border-radius: 40px;
      }
    }
  }
  @include breakpoint($phone) {
    margin-top: 80px;
  }
}

footer {
  margin-top: 143px;
  background: $blue;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 130px 0 45px 0;
  @include breakpoint($phone) {
    margin-top: 80px;
  }
  .to-top {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-top: -162px;
    margin-bottom: 90px;
    cursor: pointer;
    font-size: 12px;
    color: white;
    transition: 400ms ease;
    img {
      margin-bottom: 12px;
    }
    &:hover {
      transform: translateY(-10px);
    }
  }
  .footer-title {
    font-size: 48px;
    display: flex;
    flex-direction: column;
    align-items: center;
    span {
      display: block;
      font-size: 21px;
      text-align: center;
      margin: 26px 0 37px 0;
      font-weight: 300;
      letter-spacing: 2px;
      line-height: 2rem;
      &.phone {
        display: none;
        @include breakpoint($tablet) {
          display: block;
        }
      }
      &.pc {
        @include breakpoint($tablet) {
          display: none;
        }
      }
    }
    @include breakpoint($tablet) {
      font-size: 36px;
      padding: 0 10%;
      span {
        font-size: 18px;
      }
    }
  }
  .footer-btn {
    background: $g-blue;
    text-decoration: none;
    width: 250px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    border-radius: 20px;
    letter-spacing: 3px;
    margin-bottom: 88px;
    transition: 500ms ease;
    &:hover{
      background: $g-orange;
    }
  }
  .footer-wrapper {
    display: flex;
    justify-content: space-between;
    max-width: 1024px;
    margin-bottom: 87px;
    padding: 0 24px;
    .wrap {
      width: 31%;
      display: flex;
      flex-direction: column;
      justify-items: center;
      align-items: center;
      text-align: center;
      h3 {
        font-size: 30px;
        letter-spacing: 3px;
        font-weight: 400;
        @include breakpoint($tablet) {
          font-size: 20px;
        }
      }
      p {
        font-size: 16px;
        letter-spacing: 1.5px;
        line-height: 1.5rem;
        font-weight: 300;
      }
    }
    @include breakpoint($phone) {
      flex-direction: column;
      align-items: center;
      margin-bottom: 40px;
      .wrap {
        width: 95%;
        margin-bottom: 35px;
      }
    }
  }
  .info-wrapper {
    display: flex;
    .copy {
      font-weight: 500;
    }
    a,address {
      font-weight: 300;
      font-style: unset;
      display: block;
      @include breakpoint($tablet) {
        margin-top: 15px;
      }
    }
    address {
      margin: 0 2rem;
      @include breakpoint($tablet) {
        margin-top: 15px;
      }
    }
    a {
      color: white;
      text-decoration: none;
    }
    @include breakpoint($tablet) {
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }
}
.sec-ul {
  @include breakpoint($tablet) {
    padding-left: 0;
  }
}
